import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { RappelStockComponent } from './rappel-stock.component';
const routes: Routes = [
  {
    path: '',
    component: RappelStockComponent
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class RappelStockRoutingModule { }
